.not-found {
  &__content {
    padding-top: 40px;
    @include screen(laptop) {
      padding-top: 55px;
    }
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__image {
    img {
      width: 100%;
      max-width: 180px;
      @include screen(laptop) {
        max-width: 367px;
      }
    }
  }
  &__title {
    margin-top: 35px;
    text-align: center;
    font-family: $gotham;
    font-size: 24px;
    font-weight: 500;
    line-height: 28px;
    @include screen(laptop) {
      line-height: 36px;
      font-size: 38px;
      margin-top: 60px;
    }
  }
  &__text {
    margin-top: 15px;
    text-align: center;
    max-width: 286px;
    font-size: 14px;
    line-height: 21px;
    @include screen(laptop) {
      margin-top: 20px;
      font-size: 18px;
      max-width: 404px;
      line-height: 28px;
    }
  }
  &__link {
    margin-top: 40px;
    background-color: $text;
    border-radius: 12px;
    font-family: $gotham;
    padding: 20px 26px;
    font-weight: 500;
    color: $white;
    font-size: 11px;
    line-height: 10px;
    text-transform: uppercase;
    @include screen(laptop) {
      margin-top: 35px;
      font-size: 14px;
      line-height: 13px;
    }
  }
}