.breadcrumbs {
  padding-top: 20px;
  @include screen(laptop) {
    padding-top: 25px;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 6px;
    a {
      color: #817F87; 
      display: flex;
      align-items: center;
      @include screen(laptop) {
        transition: color $transition-default;
        &:hover {
          color: $text;
        }
      }
    }
  }
}