.card {
  height: 100%;
  border-radius: 24px;
  padding: 0 10px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  @include screen(laptop) {
    padding: 0 19px 30px;
    border-radius: 42px;
  }
  &:hover {
    .card {
      &__image {
        @include screen(laptop) {
          &:before {
            width: 100%;
            height: 100%;
            box-shadow: 0px 6px 13px 0px #A6A4B21A, 0px 24px 24px 0px #A6A4B217, 0px 55px 33px 0px #A6A4B20D, 0px 98px 39px 0px #706C8503, 0px 153px 43px 0px #706C8500;
          }
        }
      }
      &__favorites {
        opacity: 1;
        right: 40px;
      }
      &__tags {
        @include screen(laptop) {
          left: -4px;
        }
      }
      &__button {
        opacity: 1;
      }
    }
  }
  &__content {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__image {
    display: flex;
    min-height: 110px;
    max-height: 110px;
    margin-top: 30px;
    align-items: flex-end;
    @include screen(laptop) {
      max-height: 210px;
      min-height: 210px;
    }
    &:before {
      content: '';
      left: 50%;
      transform: translateX(-50%);
      position: absolute;
      top: 0;
      width: 95%;
      transition: $transition-default;
      height: 115px;
      border-radius: 24px;
      background-color: #F3F2F9;
      @include screen(laptop) {
        border-radius: 42px;
        height: 200px;
        width: 80%;
      }
    }
    img {
      z-index: 2;
      position: relative;
      object-position: bottom;
      object-fit: contain;
      min-height: 110px;
      max-height: 110px;
      width: 100%;
      @include screen(laptop) {
        max-height: 210px;
      }
    }
  }
  &__tags {
    position: absolute;
    left: 2px;
    top: 17px;
    z-index: 3;
    transition: $transition-default;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
    @include screen(laptop) {
      top: 35px;
      left: 31px;
    }
  }
  &__favorites {
    position: absolute;
    z-index: 2;
    top: 12px;
    right: 15px;
    opacity: 0;
    transition: $transition-default;
    @include screen(laptop) {
      top: 35px;
      right: 50px;
    }
    svg {
      width: 20px;
      height: 18px;
      @include screen(laptop) {
        width: 24px;
        height: 24px;
      }
    }
  }
  &__rating {
    display: flex;
    align-items: center;
    gap: 2px;
    margin-top: 21px;
    @include screen(laptop) {
      margin-top: 32px;
      gap: 4px;
    }
    svg {
      width: 10px;
      height: 10px;
      @include screen(laptop) {
        width: auto;
        height: auto;
      }
    }
  }
  &__description {
    text-align: center;
    font-size: 14px;
    margin-top: 8px;
    line-height: 20px;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    overflow: hidden;
    @include screen(laptop) {
      font-size: 18px;
      line-height: 27px;
      margin-top: 15px;
      -webkit-line-clamp: 3;
    }
  }
  &__brand {
    text-align: center;
    color: #545459;
    font-size: 12px;
    line-height: 15px;
    margin-top: 8px;
    @include screen(laptop) {
      margin-top: 15px;
      font-size: 18px;
      line-height: 23px;
    }
  }
  &__price {
    font-family: $gotham;
    font-weight: 500;
    margin-top: 15px;
    font-size: 14px;
    line-height: 13px;
    font-weight: 700;
    text-align: center;
    @include screen(laptop) {
      line-height: 17px;
      font-size: 18px;
    }
  }
  &__icon {
    min-width: 32px;
    min-height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $white;
    border-radius: 8px;
    @include screen(laptop) {
      background-color: transparent;
      min-width: auto;
      min-height: 100%;
      border-radius: 0;
    }
  }
  &__button {
    background-color: #F2F3F5;
    border-radius: 8px;
    padding: 11px 18px;
    display: inline-flex;
    opacity: 1;
    align-items: center;
    gap: 8px;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 11px;
    line-height: 11px;
    margin-top: 20px;
    text-align: center;
    font-family: $gotham;
    @include screen(laptop) {
      opacity: 0;
    }
    span {
      margin-top: 2px;
    }
    svg {
      path {
        stroke: $text;
      }
    }
    @include screen(laptop) {
      background-color: $text;
      line-height: 10px;
      transition: opacity $transition-default;
      color: $white;
      border-radius: 12px;
      font-size: 11px;
      padding: 12.5px 20px;
      svg {
        path {
          stroke: $white;
        }
      }
    }
  }
}