.hero-category {
  position: relative;
  &__bg {
    position: absolute;
    inset: 0;
    height: 100%;
    width: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__slider {
    margin-top: 20px;
    display: flex;
    overflow-x: auto;
    gap: 12px;
    padding-bottom: 10px;
    align-items: center;
    @include screen(laptop) {
      display: none;
    }
  }
  &__slide {
    min-width: min-content;
    min-height: 52px;
    border-radius: 11px;
    position: relative;
    overflow: hidden;
    img {
      position: absolute;
      inset: 0;
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
    span {
      display: inline-block;
      padding: 17px 54px;
      z-index: 2;
      position: relative;
      color: $white;
      font-family: $gotham;
      font-size: 16px;
      font-weight: 700;
      line-height: 18px;
      text-align: center;
      white-space: nowrap;
    }
  }
  &__wrapper {
    position: relative;
    z-index: 2;
    text-align: center;
    padding: 52px 0;
    @include screen(laptop) {
      padding: 122px 0;
    }
  }
  &__title {
    font-family: $gotham;
    color: $white;
    font-weight: 700;
    font-size: 24px;
    line-height: 27px;
    @include screen(laptop) {
      font-size: 48px;
      line-height: 50px;
    }
  }
}