.callback {
  &__wrapper {
    padding: 37px 15px;
    border-top: 1px solid #DAD8E1;
    border-bottom: 1px solid #DAD8E1;
    display: grid;
    gap: 26px;
    @include screen(laptop) {
      gap: 60px;
      grid-template-columns: repeat(12, minmax(0, 1fr));
      padding: 70px 122px;
    }
    h2 {
      font-family: $gotham;
      font-size: 18px;
      line-height: 23px;
      font-weight: 500;
      @include screen(laptop) {
        font-size: 28px;
        grid-column: span 5 / span 5;
        line-height: 36px;
      }
    }
  }
  &__input {
    border-radius: 14px;
    border: 1px solid #DAD8E1;
    padding: 11px 25px;
    width: 100%;
    padding-right: 170px;
    @include screen(laptop) {
      border-radius: 18px;
      padding: 16px 25px;
    }
  }
  &__form {
    display: flex;
    max-width: 682px;
    width: 100%;
    margin-left: auto;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    gap: 18px;
    @include screen(laptop) {
      grid-column: span 7 / span 7;
    }
  }
  &__label {
    width: 100%;
    display: flex;
    align-items: center;
  }
  &__policy {
    position: relative;
    display: flex;
    align-items: center;
    cursor: pointer;
    padding-left: 32px;
    input {
      position: absolute;
      top: 0;
      left: 0;
      visibility: hidden;
      opacity: 0;
      &:checked + span {
        &:before {
          background-color: #545163;
        }
        &:after {
          opacity: 1;
        }
      }
    }
    span {
      position: relative;
      font-size: 14px;
      line-height: 21px;
      @include screen(laptop) {
        font-size: 16px;
        line-height: 21px;
      }
      a {
        text-decoration: underline;
      }
      &:before {
        content: '';
        left: -32px;
        position: absolute;
        border: 1px solid #545163;
        border-radius: 6px;
        width: 20px;
        transition: background-color $transition-default;
        height: 20px;
      }
      &:after {
        content: url('data:image/svg+xml;base64,');
        position: absolute;
        left: -27px;
        top: -2px;
        opacity: 0;
        transition: opacity $transition-default;
      }
    }
  }
  &__button {
    position: absolute;
    background-color: $text;
    border-radius: 8px;
    width: 36px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    right: 7px;
    color: $white;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 7px;
    line-height: 7px;
    text-align: center;
    font-family: $gotham;
    span {
      display: none;
      @include screen(laptop) {
        display: block;
      }
    }
    svg {
      @include screen(laptop) {
        display: none;
      }
    }
    @include screen(laptop) {
      line-height: 10px;
      display: inline-block;
      width: auto;
      height: auto;
      border-radius: 12px;
      font-size: 11px;
      padding: 16px 26px;
    }
  }
}