.hero {
  padding-top: 25px;
  @include screen(laptop) {
    padding-top: 55px;
  }
  &__wrapper {
    display: grid;
    background-color: #FFE6E7;
    border-radius: 40px;
    overflow: hidden;
    @include screen(laptop) {
      border-radius: 46px;
      grid-template-columns: repeat(12, minmax(0, 1fr));
    }
  }
  &__slider {
    position: relative;
    overflow: hidden;
    padding: 30px 37px 20px;
    @include screen(laptop) {
      grid-column: span 8 / span 8;
      padding: 65px 110px;
    }
    @include screen(desktop) {
      max-height: 442px;
    }
    .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0;
    }
    .swiper-pagination {
      position: relative;
      bottom: 0;
      left: 0;
      width: auto;
      display: flex;
      margin-top: 27px;
      gap: 7px;
      @include screen(laptop) {
        margin-top: 60px;
      }
      &-bullet {
        width: 8px;
        opacity: 1;
        height: 8px;
        background-color: #DAD8E1;
        &-active {
          background-color: #9E96B4;
        }
      }
    }
    &-main {
      .hero__navigation {
        left: 0;
        margin-top: 26px;
        right: auto;
        justify-content: flex-start;
        @include screen(laptop) {
          margin-top: 63px;
        }
        .swiper-button-prev,
        .swiper-button-next {
          display: none;
          @include screen(laptop) {
            display: flex;
          }
        }
        .swiper-pagination {
          margin-left: 0;
          margin-right: 0;
          @include screen(laptop) {
            margin: 0 48px;
          }
        }
      }
    }
    &-small {
      background-color: #FFF3F3;
      padding: 27px 36px 25px;
      @include screen(laptop) {
        grid-column: span 4 / span 4;
        padding: 60px 42px 60px 80px;
      }
      .hero__navigation {
        @include screen(laptop) {
          right: 0px;
        }
        @include screen(desktop) {
          right: 40px;
        }
      }
      .hero {
        &__slide {
          display: flex;
          gap: 37px;
          @include screen(laptop) {
            gap: 24px;
          }
        }
        &__content {
          display: flex;
          align-items: center;
          flex-direction: column;
          justify-content: center;
          h2 {
            font-family: $gotham;
            text-align: center;
            font-size: 16px;
            line-height: 20px;
            font-weight: 500;
            @include screen(laptop) {
              font-size: 22px;
              line-height: 28px;
            }
          }
          p {
            text-align: center;
          }
          a {
            display: none;
            border: 1px solid $text;
            background-color: transparent;
            color: $text;
            @include screen(laptop) {
              display: block;
            }
          }
        }
      }
    }
  }
  &__navigation {
    display: flex;
    align-items: center;
    justify-content: center;
    left: auto;
    margin-top: 17px;
    position: relative;
    @include screen(laptop) {
      justify-content: flex-end;
      bottom: 10px;
      right: 56px;
      margin-top: 60px;
    }
    .swiper-pagination {
      margin: 0 48px;
    }
    .swiper-button-prev,
    .swiper-button-next {
      position: relative;
      display: flex;
      left: auto;
      align-items: center;
      justify-content: center;
      right: auto;
      margin-top: 0;
      width: 24px;
      height: 24px;
      &:after,
      &:before {
        display: none;
      }
      svg {
        height: 12px;
        path {
          @include screen(laptop) {
            transition: stroke $transition-default;
          }
        }
      }
      @include screen(laptop) {
        &:hover {
          svg {
            path {
              stroke: #F3F2F9;
            }
          }
        }
      }
    }
  }
  &__bg {
    position: absolute;
    right: 0;
    bottom: 0;
    img {
      max-width: 80%;
      margin-left: auto;
    }
  }
  &__content {
    h2 {
      font-family: $gotham;
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;
      @include screen(laptop) {
        font-size: 38px;
        line-height: 43px;
      }
    }
    p {
      margin-top: 10px;
      font-size: 12px;
      line-height: 18px;
      @include screen(laptop) {
        font-size: 18px;
        margin-top: 20px;
        line-height: 27px;
      }
    }
    a {
      background-color: $text;
      border-radius: 8px;
      padding: 11px 18px;
      display: inline-block;
      color: $white;
      text-transform: uppercase;
      font-weight: 500;
      font-size: 7px;
      line-height: 7px;
      margin-top: 15px;
      text-align: center;
      font-family: $gotham;
      @include screen(laptop) {
        line-height: 10px;
        border-radius: 12px;
        font-size: 11px;
        padding: 16px 26px;
        margin-top: 40px;
      }
    }
  }
  &__image {
    min-width: 40px;
    position: relative;
    display: flex;
    bottom: -20px;
    align-items: center;
    justify-content: center;
    @include screen(laptop) {
      bottom: -40px;
      min-width: 100px;
    }
  }
}