.filters {
  margin-top: 20px;
  @include screen(laptop) {
    margin-top: 40px;
  }
  &__items {
    display: none;
    @include screen(laptop) {
      align-items: center;
      gap: 24px;
      display: grid;
      grid-template-columns: repeat(5, minmax(0, 1fr));
    }
  }
  &__item {
    flex: 1;
    position: relative;
  }
  &__labels {
    padding: 10px 20px 5px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }
  &__label {
    cursor: pointer;
    padding-left: 32px;
    width: 100%;
    position: relative;
    input {
      position: absolute;
      visibility: hidden;
      width: 0;
      height: 0;
      opacity: 0;
      &:checked + span {
        &:before {
          background-color: #545163;
        }
        &:after {
          opacity: 1;
        }
      }
    }
    span {
      position: relative;
      &:before {
        content: '';
        left: -32px;
        position: absolute;
        border: 1px solid #545163;
        border-radius: 6px;
        width: 20px;
        top: 2px;
        transition: background-color $transition-default;
        height: 20px;
      }
      &:after {
        content: url('data:image/svg+xml;base64,');
        position: absolute;
        left: -27px;
        top: -2px;
        opacity: 0;
        transition: opacity $transition-default;
      }
    }
  }
  &__current {
    border: 1px solid #DAD8E1;
    padding: 16.5px 25px;
    min-height: 57px;
    cursor: pointer;
    gap: 13px;
    border-radius: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    input {
      position: absolute;
      visibility: hidden;
      width: 0;
      height: 0;
      opacity: 0;
      background-color: transparent;
    }
    svg {
      transition: transform $transition-default;
      min-width: 13px;
    }
    p {
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden; 
      max-width: 170px;
      font-size: 14px;
      line-height: 20px;
      @include screen(laptop) {
        font-size: 16px;
      }
    }
    &.active {
      svg {
        transform: rotate(180deg);
      }
    }
  }
  &__dropdown {
    padding: 10px 0 15px;
    border-radius: 12px;
    max-height: 300px;
    display: none;
    overflow-y: auto;
    position: absolute;
    box-shadow: 0px 0px 20px 0px #36413D1F;
    top: 69px;
    border: 1px solid #DAD8E1;
    z-index: 5;
    background-color: $white;
    width: 100%;
    &.active {
      display: block;
    }
    p {
      padding: 10px 25px;
      transition: background-color $transition-default, color $transition-default;
      cursor: pointer;
      &.main {
        font-weight: 600;
      }
      &.active {
        background-color: #F5F4F6;
      }
      @include screen(laptop) {
        &:hover {
          background-color: #F5F4F6;
        }
      }
    }
  }
  &__info {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    column-gap: 34px;
    margin-top: 15px;
    justify-content: space-between;
    flex-direction: row-reverse;
    @include screen(laptop) {
      padding-top: 25px;
      margin-top: 38px;
      flex-direction: row;
      flex-wrap: wrap;
      gap: 0;
      border-top: 1px solid #DAD8E1;
    }
  }
  &__area {
    padding: 15px 25px 10px;
    .noUi-horizontal .noUi-handle {
      width: 20px;
      height: 20px;
      top: -9px;
      right: -9px;
    }
    .noUi-horizontal {
      box-shadow: none;
      height: 2px;
      background-color: #DAD8E1;
      border: none;
      border-radius: 0;
    }
    .noUi-connect {
      background-color: #887DA7;
    }
    .noUi-handle {
      border: 3px solid $white;
      border-radius: 7px;
      background-color: #887DA7;
      box-shadow: none;
    }
    .noUi-handle:before, .noUi-handle:after {
      display: none;
    }
  }
  &__costs {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: flex-start;
  }
  &__value {
    display: flex;
    gap: 15px;
    align-items: center;
    span {
      text-transform: lowercase;
    }
  }
  &__cost {
    max-width: 162px;
    width: 100%;
    background-color: #F2F3F5;
    padding: 10px 15px 9px;
    border-radius: 12px;
  }
  &__count {
    order: 2;
    margin-top: 20px;
    border-top: 1px solid #DAD8E1;
    width: 100%;
    font-size: 14px;
    text-align: center;
    padding-top: 20px;
    line-height: 19px;
    @include screen(laptop) {
      font-size: 16px;
      width: auto;
      margin-top: 0;
      border-top: none;
      display: flex;
      align-items: center;
      order: inherit;
      padding-top: 0;
      line-height: 21px;
      min-height: 57px;
    }
  }
  &__tags {
    display: flex;
    margin-top: 20px;
    width: 100%;
    flex-wrap: wrap;
    gap: 15px;
  }
  &__remove {
    cursor: pointer;
  }
  &__tag {
    padding: 13px 16px;
    border-radius: 10px;
    display: flex;
    background-color: #F2F3F5;
    gap: 10px;
    align-items: center;
  }
  &__clear {
    padding: 13px 16px;
    order: 2;
    display: none;
    align-items: center;
    cursor: pointer;
    gap: 8px;
    &.active {
      display: flex;
    }
  }
  &__trigger {
    background-color: $text;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 12px;
    cursor: pointer;
    height: 42px;
    width: 47px;
    @include screen(laptop) {
      display: none;
    }
  }
  &__sorting {
    flex: 1;
    width: 100%;
    @include screen(laptop) {
      min-width: 250px;
      max-width: 250px;
    }
    .filters__current {
      background-color: #F2F3F5;
      border: none;
      padding: 11px 14px;
      min-height: 42px;
      @include screen(laptop) {
        min-height: 57px;
      }
    }
    .filters__dropdown {
      min-width: 309px;
      right: 0;
    }
  }
}