.products-slider {
  &__slider {
    margin-top: 25px;
    position: relative;
    @include screen(laptop) {
      margin-bottom: -80px;
      margin-top: 50px;
    }
    .swiper {
      @include screen(laptop) {
        padding: 0 40px 80px;
        margin: 0 -40px;
      }
      &-wrapper {
        align-items: stretch;
      }
      &-slide {
        padding-top: 14px;
        height: auto;
        border-radius: 24px;
        @include screen(laptop) {
          display: block;
          padding-top: 0;
          border-radius: 42px;
        }
      }
    }
    .swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
      margin: 0;
    }
    .swiper-button-prev,
    .swiper-button-next {
      margin-top: 0;
      width: 24px;
      transform: translateY(-50%);
      height: 24px;
      display: none;
      align-items: center;
      justify-content: center;
      @include screen(laptop) {
        display: flex;
      }
      &:after,
      &:before {
        display: none;
      }
      svg {
        height: 12px;
        path {
          @include screen(laptop) {
            transition: stroke $transition-default;
          }
        }
      }
      @include screen(laptop) {
        &:hover {
          svg {
            path {
              stroke: #F3F2F9;
            }
          }
        }
      }
    }
  }
  .card__button {
    padding: 5px 24px 5px 5px;
    @include screen(laptop) {
      padding: 12.5px 20px;
    }
  }
}