* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
}

textarea,
input,
button,
select,
a {
  outline: none;
  border-radius: 0;

  font-weight: 400;
  font-family: $opensans;
  touch-action: manipulation;
}

html,
body {
  height: 100%;

  font-family: $opensans;
  color: $text;
}

.render {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999;

  width: 100%;
  height: 100%;

  background-color: $white;
}

main {
  @include screen(laptop) {
    margin-top: 170px;
  }
}

::-webkit-scrollbar {
  width: 100%;
  height: 6px;
}
::-webkit-scrollbar-track {
  background-color: transparent;
}
::-webkit-scrollbar-thumb {
  border-radius: 6px;
  background-color: #DAD8E1;
  border: 2px solid $white;
}

.gapper {
  margin-top: 40px;
  @include screen(laptop) {
    margin-top: 80px;
  }
}

.title {
  text-align: center;
  font-family: $gotham;
  font-weight: 500;
  position: relative;
  font-size: 18px;
  line-height: 21px;
  @include screen(laptop) {
    font-size: 32px;
    line-height: 40px;
  }
  &:before {
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -8px;
    border-radius: 2px;
    width: 48px;
    background-color: #B5ABD0;
    height: 2px;
    @include screen(laptop) {
      width: 96px;
    }
  }
  &-no-border {
    &:before {
      display: none;
    }
  }
}

.wrapper {
  margin: 0 auto;
  padding: 0 #{$container-padding};
  width: 100%;
  max-width: calc(#{$screen-tablet} + #{$container-padding * 2});
  @include screen(laptop) {
    max-width: calc(#{$screen-laptop} + #{$container-padding * 2});
  }
  @include screen(desktop) {
    max-width: calc(#{$screen-desktop} + #{$container-padding * 2});
  }
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}

.button {
  margin-top: 25px;
  font-size: 11px;
  line-height: 10px;
  z-index: 5;
  @include screen(laptop) {
    margin-top: 20px;
  }
  &-more {
    background-color: $text;
    border-radius: 12px;
    padding: 17px 26px;
    display: inline-flex;
    align-items: center;
    gap: 10px;
    color: $white;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 11px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    line-height: 10px;
    text-align: center;
    font-family: $gotham;
    @include screen(laptop) {
      line-height: 13px;
      border-radius: 12px;
      font-size: 14px;
      padding: 18px 26px;
    }
  }
}