.video-blog {
  &__items {
    display: flex;
    gap: 15px;
    margin-top: 40px;
    overflow-x: auto;
    @include screen(laptop) {
      justify-content: center;
      gap: 40px;
      overflow-x: inherit;
    }
  }
  &__item {
    min-width: 74px;
    max-width: 74px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    width: 100%;
    height: 100%;
    img {
      padding: 5px;
      border-radius: 26px;
      min-width: 74px;
      max-width: 74px;
      object-fit: cover;
      border: 2px solid #B5ABD0;
      @include screen(laptop) {
        min-width: 96px;
        border-radius: 32px;
        border: 3px solid #B5ABD0;
        max-width: 96px;
      }
    }
    span {
      text-align: center;
      display: inline-block;
      line-height: 21px;
    }
  }
}