.social-banner {
  &__wrapper {
    display: grid;
    background-color: #F2F3F5;
    border-radius: 50px;
    padding: 30px 25px;
    grid-template-columns: repeat(1, minmax(0, 1fr));
    align-items: center;
    @include screen(laptop) {
      padding: 43px 40px;
      gap: 40px;
      grid-template-columns: repeat(2, minmax(0, 1fr));
    }
    @include screen(desktop) {
      padding: 43px 80px;
      gap: 113px;
    }
  }
  &__images {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
    gap: 9px;
    @include screen(laptop) {
      gap: 24px;
    }
    img {
      object-fit: cover;
      border-radius: 30px;
      width: 100%;
      height: 100%;
      max-height: 100px;
      @include screen(laptop) {
        max-height: 162px;
      }
      &:first-child {
        grid-column: span 2 / span 2;
      }
      &:last-child {
        grid-column: span 1 / span 1;
      }
    }
  }
  &__info {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 30px;
    gap: 20px;
    @include screen(laptop) {
      gap: 54px;
      margin-top: 0;
      flex-direction: row;
    }
    h3 {
      font-family: $gotham;
      text-align: center;
      font-size: 18px;
      line-height: 25px;
      font-weight: 500;
      @include screen(laptop) {
        text-align: left;
        font-size: 22px;
        line-height: 28px;
      }
      @include screen(desktop) {
        max-width: 308px;
        font-size: 28px;
        line-height: 39px;
      }
    }
  }
  &__items {
    display: flex;
    align-items: flex-start;
    gap: 26px;
    @include screen(laptop) {
      gap: 40px;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    span {
      display: inline-block;
      line-height: 21px;
      color: #545459;
      margin-top: 15px;
      font-size: 14px;
      @include screen(laptop) {
        font-size: 16px;
      }
    }
  }
  &__icon {
    width: 56px;
    height: 56px;
    border-radius: 19px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $white;
  }
}