.header {
  background-color: $white;
  &-top {
    padding: 13px 0;
    background-color: #EEECF7;
    display: none;
    @include screen(laptop) {
      display: block;
    }
  }
  &-bottom {
    width: 100%;
    top: 0;
    left: 0;
    transition: $transition-default;
    z-index: 102;
    &.active {
      @include screen(laptop) {
        margin-top: -51px;
      }
    }
    @include screen(laptop) {
      top: auto;
      position: fixed;
    }
  }
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__phone {
    font-size: 18px;
    font-weight: 600;
    line-height: 24px;
    display: none;
    @include screen(laptop) {
      align-items: center;
      display: flex;
      gap: 10px;
    }
  }
  &__phonemob {
    display: block;
    @include screen(laptop) {
      display: none;
    }
    svg {
      width: 17px;
      height: 17px;
    }
  }
  &__secondary {
    display: inline-flex;
    align-items: center;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    gap: 60px;
    a {
      font-size: 17px;
      line-height: 23px;
      &.bold {
        font-weight: 600;
      }
    }
  }
  &__langs {
    gap: 12px;
    display: none;
    @include screen(laptop) {
      align-items: center;
      display: flex;
    }
  }
  &__langsmob {
    position: absolute;
    right: 56px;
    @include screen(laptop) {
      display: none;
    }
  }
  &__lang {
    display: none;
    @include screen(laptop) {
      display: block;
    }
    &.active {
      border-bottom: 1px solid #8C889D;
      display: block;
      color: #8C889D;
      @include screen(laptop) {
        border-color: $text;
        color: $text;
      }
    }
  }
  &__primary {
    display: flex;
    margin-top: 58px;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid #DAD8E1;
    gap: 25px;
    overflow-x: auto;
    &.active {
      display: none;
    }
    @include screen(laptop) {
      overflow-x: inherit;
      margin-top: 0;
    }
  }
  &__submenu {
    gap: 57px;
    display: none;
    @include screen(laptop) {
      display: flex;
      align-items: center;
    }
    a {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 8px;
      font-size: 16px;
      line-height: 21px;
    }
  }
  &__logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    @include screen(laptop) {
      transform: translateX(0%);
      position: static;
    }
    img {
      max-width: 128px;
      @include screen(laptop) {
        max-width: 100%;
      }
    }
  }
  &__dropdown {
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    box-shadow: 0px 0px 30px 0px #d8d8d8;
    padding: 49px 80px;
    border-radius: 26px;
    position: absolute;
    transition: opacity $transition-default, z-index $transition-default, visibility $transition-default;
    top: 60px;
    background-color: $white;
    display: none;
    @include screen(laptop) {
      display: flex;
    }
    &-right {
      right: 0;
    }
    h3 {
      font-size: 20px;
      font-family: $gotham;
      line-height: 19px;
      font-weight: 500;
      position: relative;
      width: 100%;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        bottom: -8px;
        border-radius: 2px;
        width: 53px;
        background-color: #B5ABD0;
        height: 2px;
      }
    }
    .header__categories {
      border-right: 1px solid #DAD8E1;
      padding-right: 60px;
      .header__items {
        flex-direction: row;
        flex-wrap: nowrap;
        & > div {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          row-gap: 15px;
        }
      }
    }
    .header__types {
      padding-left: 60px;
    }
  }
  &__items {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 15px;
    column-gap: 80px;
    flex-wrap: wrap;
  }
  &__item {
    font-size: 18px;
    line-height: 24px;
    white-space: inherit;
    position: relative;
    @include screen(laptop) {
      &:hover {
        -webkit-text-stroke: 0.5px black;
      }
    }
  }
  &__link {
    font-size: 14px;
    line-height: 100%;
    position: relative;
    padding: 10.5px 0;
    height: 100%;
    white-space: nowrap;
    @include screen(laptop) {
      padding: 16px 0;
      font-size: 20px;
      &:hover {
        .header__dropdown {
          opacity: 1;
          visibility: visible;
          z-index: 3;
        }
      }
    }
    & > a {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      span {
        bottom: -10px;
        position: absolute;
        left: 50%;
        width: 48px;
        transform: translateX(-50%);
        &:before {
          content: '';
          position: absolute;
          border-radius: 2px;
          bottom: 0;
          left: 0;
          width: 0;
          background-color: #B5ABD0;
          height: 2px;
          @include screen(laptop) {
            transition: width $transition-default;
          }
        }
      }
      &:after {
        content: '';
        position: absolute;
        bottom: -40px;
        left: 0;
        display: none;
        width: 100%;
        height: 40px;
        @include screen(laptop) {
          display: block;
        }
      }
    }
    &.bordered {
      a {
        padding: 8px 14px;
        border-radius: 12px;
        display: inline-block;
        border: 1px solid #A2A0A8;
        &:before {
          display: none;
        }
      }
    }
    @include screen(laptop) {
      &:hover {
        a {
          span {
            &:before {
              width: 48px;
            }
          }
        }
      }
    }
  }
  &__upper {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20.5px 19px;
    position: fixed;
    width: 100%;
    top: 0;
    background-color: $white;
    z-index: 102;
    left: 0;
    @include screen(laptop) {
      padding: 23px 0;
      position: static;
    }
  }
  &__back {
    @include screen(laptop) {
      display: none;
    }
  }
  &__search {
    display: none;
    position: fixed;
    z-index: 2;
    padding: 20px;
    gap: 15px;
    align-items: center;
    width: 100%;
    top: 0;
    left: 0;
    background-color: $white;
    &.active {
      display: flex;
    }
    @include screen(laptop) {
      gap: 0;
      position: relative;
      max-width: 317px;
      padding: 0;
      display: flex;
      width: 100%;
      left: auto;
      top: auto;
    }
    input {
      border: 1px solid #DAD8E1;
      background-color: #F5F4F6;
      padding: 12px 20px;
      width: 100%;
      border-radius: 15px;
      @include screen(laptop) {
        max-width: 317px;
        width: 100%;
      }
      &:active,
      &:focus {
        & + button {
          display: none;
        }
      }
    }
  }
  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 40px;
    @include screen(laptop) {
      right: 20px;
    }
  }
  &__trigger {
    right: 0;
    @include screen(laptop) {
      display: none;
    }
    svg {
      width: 17px;
      height: 17px;
    }
  }
  &-sticky {
    background-color: $white;
    position: fixed;
    border-top: 1px solid #DAD8E1;
    width: 100%;
    left: 0;
    z-index: 102;
    bottom: 0;
    @include screen(laptop) {
      display: none;
    }
    &__profile {
      img {
        max-width: 20px;
        max-height: 20px;
        width: 100%;
        height: 100%;
        object-fit: contain;
        aspect-ratio: 1 / 1;
      }
    }
    &__number {
      top: 4px;
      right: 6px;
      position: absolute;
      border: 1px solid $white;
      padding: 4px 5px;
      border-radius: 4px;
      color: $white;
      font-size: 8px;
      line-height: 7px;
      background-color: #F6632A;
    }
    &__wrapper {
      display: flex;
      align-items: stretch;
      justify-content: space-between;
      padding: 7px 0 6px;
      a,
      button {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 10px;
        line-height: 13px;
        flex-direction: column;
        gap: 2px;
        svg {
          path {
            &.hidden {
              opacity: 0;
            }
          }
        }
        span {
          position: relative;
          &:before {
            content: '';
            position: absolute;
            left: 50%;
            opacity: 0;
            bottom: -8px;
            border-radius: 2px;
            width: 50%;
            background-color: #B5ABD0;
            height: 2px;
            transform: translateX(-50%);
          }
        }
        &:not(.main) {
          &:hover {
            span {
              &:before {
                opacity: 1;
              }
            }
            svg {
              path {
                &.hidden {
                  opacity: 1;
                }
              }
            }
          }
        }
        &.main {
          border-radius: 12px;
          padding: 7px 12px;
          background-color: #F2F3F5;
          &.active {
            color: $white;
            background-color: $purple;
            svg {
              height: 12px;
              path {
                stroke: $white;
              }
            }
          }
        }
      }
    }
  }
  &-mobmenu {
    display: none;
    z-index: 101;
    top: 64px;
    width: 100%;
    max-height: calc(100dvh - 135px);
    padding: 16px 0;
    overflow-y: auto;
    position: fixed;
    &.active {
      display: block;
    }
    &__bg {
      display: none;
      z-index: 100;
      background-color: #F2F3F5;
      inset: 0;
      width: 100%;
      max-height: 100dvh;
      position: fixed;
      &.active {
        display: block;
      }
    }
    &__wrapper {
      display: flex;
      flex-direction: column;
      gap: 15px;
    }
    &__block {
      box-shadow: 0px 0px 20px 0px #3836411A;
      border-radius: 30px;
      padding: 25px 19px 8px;
      background-color: $white;
      width: 100%;
      h3 {
        font-weight: 700;
        line-height: 21px;
        margin-bottom: 10px;
      }
    }
    &__link {
      padding: 8px 10px 8px 4px;
      display: flex;
      align-items: center;
      gap: 12px;
      position: relative;
      border-bottom: 1px solid #DAD8E1;
      &:last-child {
        border: none;
      }
      &-secondary {
        padding: 15px 10px 15px 4px;
      }
      &-green {
        color: $green;
        a {
          font-weight: 600;
        }
      }
      svg {
        transition: transform $transition-default;
        rect {
          transition: fill $transition-default;
        }
        path {
          transition: stroke $transition-default;
        }
      }
      &.active {
        .header-mobmenu__button {
          svg {
            transform: rotate(180deg);
            rect {
              fill: #887DA7;
            }
            path {
              stroke: $white;
            }
          }
        }
      }
    }
    &__button {
      position: absolute;
      right: 10px;
    }
    &__dropdown {
      padding-left: 61px;
      display: none;
      flex-direction: column;
      margin-top: 15px;
      gap: 15px;
      &.active {
        display: flex;
      }
      a {
        font-size: 13px;
        line-height: 17px;
        &.active {
          font-weight: 600;
        }
      }
    }
  }
}