.faq {
  &__items {
    margin-top: 20px;
    display: grid;
    gap: 15px;
    @include screen(laptop) {
      margin-top: 40px;
      gap: 20px;
    }
  }
}

details {
  width: 100%;
  p {
    padding: 15px 20px 15px 22px;
    font-size: 14px;
    line-height: 23px;
    max-width: 1005px;
    @include screen(laptop) {
      padding: 25px 42px 25px 54px;
      font-size: 18px;
      line-height: 28px;
    }
  }
  summary {
    border-radius: 8px;
    padding: 15px 20px 15px 22px;
    font-family: $gotham;
    font-size: 16px;
    line-height: 21px;
    cursor: pointer;
    background-color: #F2F3F5;
    list-style: none;
    &::marker,
    &::-webkit-details-marker {
      display: none;
    }
    @include screen(laptop) {
      font-size: 24px;
      line-height: 31px;
      padding: 25px 42px 25px 54px;
    }
    h6 {
      padding-right: 30px;
      position: relative;
      display: flex;
      align-items: center;
      svg {
        position: absolute;
        right: 0;
        transition: transform $transition-default;
        width: 21px;
        height: 21px;
        @include screen(laptop) {
          width: auto;
          height: auto;
        }
        path {
          transition: stroke $transition-default;
        }
        rect {
          transition: fill $transition-default;
        }
      }
    }
  }
  &[open] {
    summary {
      h6 {
        svg {
          transform: rotate(180deg);
          rect {
            fill: #887DA7;
          }
          path {
            stroke: $white;
          }
        }
      }
    }
    p {
      padding-bottom: 5px;
      @include screen(laptop) {
        padding-bottom: 15px;
      }
    }
  }
}