.cards {
  margin-top: 20px;
  @include screen(laptop) {
    margin-top: 31px;
  }
  &__wrapper {
    display: grid;
    column-gap: 17px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    row-gap: 30px;
    @include screen(laptop) {
      grid-template-columns: repeat(4, minmax(0, 1fr));
      column-gap: 0px;
      row-gap: 80px;
    }
  }
}