.brands {
  background-color: #F2F3F5;
  overflow: hidden;
  &-page {
    background-color: transparent;
    padding-top: 30px;
    @include screen(laptop) {
      padding-top: 70px;
    }
  }
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title {
    font-family: $gotham;
    font-weight: 500;
    font-size: 22px;
    line-height: 22px;
    text-align: center;
    @include screen(laptop) {
      font-size: 38px;
      line-height: 36px;
    }
  }
  &__text {
    font-size: 14px;
    margin-top: 30px;
    line-height: 23px;
    max-width: 1330px;
    @include screen(laptop) {
      line-height: 28px;
      font-size: 18px;
      margin: 40px auto 0;
      text-align: center;
    }
  }
  &__items {
    margin-top: 40px;
    gap: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    @include screen(laptop) {
      margin-top: 100px;
      gap: 100px;
    }
  }
  &__item {
    height: 105px;
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    img {
      object-fit: contain;
    }
    @include screen(laptop) {
      width: 20%;
    }
  }
  @include screen(laptop) {
    background-color: transparent;
  }
  &__slider {
    padding: 22px 0;
    @include screen(laptop) {
      padding: 39px 0;
      border-top: 1px solid #DAD8E1;
      border-bottom: 1px solid #DAD8E1;
    }
    .swiper {
      overflow: visible;
      @include screen(laptop) {
        overflow: hidden;
        padding: 0 40px;
        margin: 0 -40px;
      }
      &-wrapper {
        align-items: stretch;
        transition-timing-function: linear;
      }
      &-slide {
        height: auto;
        padding: 15px 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 20px;
        background-color: $white;
        @include screen(laptop) {
          border-radius: 0;
          padding: 0;
          background-color: transparent;
        }
        img {
          max-height: 56px;
          max-width: 100px;
          object-fit: contain;
          width: 100%;
        }
      }
    }
    .swiper-button-prev,
    .swiper-button-next {
      margin-top: 0;
      width: 24px;
      align-items: center;
      justify-content: center;
      height: 24px;
      top: 50%;
      transform: translateY(-50%);
      display: none;
      @include screen(laptop) {
        display: flex;
      }
      &:after,
      &:before {
        display: none;
      }
      svg {
        height: 12px;
        path {
          @include screen(laptop) {
            transition: stroke $transition-default;
          }
        }
      }
      @include screen(laptop) {
        &:hover {
          svg {
            path {
              stroke: #F3F2F9;
            }
          }
        }
      }
    }
  }
  &__banner {
    background-color: #F2F3F5;
    border-radius: 35px;
    margin-top: 60px;
    padding: 30px 40px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    @include screen(laptop) {
      flex-direction: row;
      margin-top: 100px;
      gap: 36px;
      padding: 9px 70px;
    }
    h2 {
      font-family: $gotham;
      font-weight: 500;
      margin-top: 5px;
      font-size: 18px;
      line-height: 23px;
      @include screen(laptop) {
        line-height: 36px;
        font-size: 28px;
      }
    }
    a {
      background-color: $text;
      border-radius: 12px;
      font-family: $gotham;
      padding: 17px;
      text-align: center;
      margin-top: 20px;
      width: 100%;
      font-weight: 500;
      color: $white;
      font-size: 11px;
      line-height: 10px;
      text-transform: uppercase;
      @include screen(laptop) {
        font-size: 14px;
        width: auto;
        margin-top: 0;
        padding: 20px 45px;
        line-height: 13px;
      }
    }
  }
}