.promotions {
  &__items {
    display: flex;
    align-items: stretch;
    gap: 16px;
    overflow-x: auto;
    padding-bottom: 20px;
    @include screen(laptop) {
      overflow-x: inherit;
      display: grid;
      padding-bottom: 0;
      gap: 24px;
      grid-template-columns: repeat(4, minmax(0, 1fr));
    }
  }
  &__title {
    font-family: $gotham;
    font-weight: 500;
    z-index: 2;
    position: relative;
    line-height: 19px;
    @include screen(laptop) {
      font-size: 28px;
      line-height: 33px;
    }
  }
  &__description {
    font-size: 12px;
    z-index: 2;
    position: relative;
    line-height: 16px;
    margin-top: 15px;
    max-width: 376px;
    @include screen(laptop) {
      margin-top: 25px;
      font-size: 16px;
      line-height: 22.4px;
    }
  }
  img {
    position: absolute;
    right: -32px;
    bottom: -19px;
    z-index: 2;
    max-width: 150px;
    @include screen(laptop) {
      position: relative;
      margin-top: 76px;
      max-width: 600px;
      width: 100%;
    }
  }
  &__footer  {
    display: flex;
    z-index: 2;
    position: relative;
    align-items: flex-start;
    gap: 16px;
    justify-content: space-between;
    margin-top: 54px;
    flex-direction: column;
    @include screen(laptop) {
      margin-top: 90px;
      align-items: center;
      flex-direction: row;
    }
  }
  &__item {
    min-width: 318px;
    border-radius: 30px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include screen(laptop) {
      border-radius: 46px;
    }
    &-yellow {
      background-color: #FFF3DD;
      &:before {
        content: url('data:image/svg+xml;base64,');
        position: absolute;
        bottom: 0;
        right: 0;
        transform: scale(0.3);
        transform-origin: bottom right;
        @include screen(laptop) {
          transform: scale(1);
        }
      }
    }
    &-green {
      background-color: #EEF9DC;
      &:before {
        content: url('data:image/svg+xml;base64,');
        position: absolute;
        top: 0;
        left: 0;
        transform: scale(0.6);
        transform-origin: top left;
        @include screen(laptop) {
          transform: scale(1);
        }
      }
    }
    &-pink {
      background-color: #FEE7EA;
      &:before {
        content: url('data:image/svg+xml;base64,');
        position: absolute;
        bottom: 0;
        right: 0;
        transform: scale(0.5);
        transform-origin: bottom right;
        @include screen(laptop) {
          transform: scale(1);
        }
      }
    }
    &-lg {
      position: relative;
      padding: 30px;
      @include screen(laptop) {
        padding: 70px 80px 60px;
      }
      .promotions__title {
        @include screen(laptop) {
          font-size: 38px;
          line-height: 36px;
        }
      }
    }
    &-md {
      position: relative;
      padding: 30px;
      @include screen(laptop) {
        padding: 55px 70px 60px;
      }
      img {
        position: absolute;
        right: -32px;
        bottom: -19px;
        max-width: 150px;
        @include screen(laptop) {
          right: 0;
          bottom: 50%;
          max-width: 100%;
          transform: translateY(50%);
          margin-top: 0;
          width: auto;
        }
      }
      .promotions__description {
        max-width: 296px;
      }
      .promotions__footer {
        @include screen(laptop) {
          margin-top: 116px;
        }
      }
    }
    &-sm.promotions__item-yellow {
      &:before {
        transform: scale(0.3);
        transform-origin: bottom right;
        @include screen(laptop) {
          transform: scale(1);
          max-width: 350px;
          max-height: 350px;
        }
      }
    }
    &-sm {
      position: relative;
      padding: 30px;
      
      @include screen(laptop) {
        padding: 45px 45px 50px;
      }
      img {
        position: absolute;
        right: -32px;
        bottom: -19px;
        margin-top: 0;
        width: auto;
      }
      .promotions__footer {
        @include screen(laptop) {
          margin-top: 111px;
        }
      }
      .promotions__description {
        max-width: 227px;
      }
    }
    &:nth-child(1) {
      grid-column: span 2 / span 2;
      grid-row: span 2 / span 2;
    }
    &:nth-child(4) {
      grid-column: span 2 / span 2;
      grid-column-start: 3;
      grid-row-start: 2;
    }
  }
  &__link  {
    background-color: transparent;
    border-radius: 0;
    padding: 0;
    color: $text;
    display: inline-flex;
    gap: 8px;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 11px;
    line-height: 10px;
    text-align: center;
    font-family: $gotham;
    span {
      margin-top: 1px;
    }
    &-black {
      @include screen(laptop) {
        background-color: $text;
        border-radius: 8px;
        color: $white;
        padding: 11px 18px;
      }
      svg {
        @include screen(laptop) {
          display: none;
        }
      }
    }
    @include screen(laptop) {
      line-height: 10px;
      border-radius: 12px;
      font-size: 11px;
      padding: 16px 26px;
    }
  }
  &__limit {
    font-size: 12px;
    line-height: 16px;
    @include screen(laptop) {
      line-height: 22px;
    }
  }
  &__texts {
    margin-top: 55px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    @include screen(laptop) {
      margin-top: 100px;
    }
  }
  &__text {
    font-size: 14px;
    line-height: 23px;
    @include screen(laptop) {
      font-size: 18px;
      line-height: 28px;
    }
    &-main {
      font-family: $gotham;
      font-size: 16px;
      line-height: 23px;
      padding-left: 32px;
      position: relative;
      @include screen(laptop) {
        font-size: 28px;
        line-height: 37px;
        padding-left: 64px;
      }
      &:before {
        content: url('data:image/svg+xml;base64,');
        position: absolute;
        left: 0;
        top: -32px;
        transform: scale(0.5);
        transform-origin: left;
        @include screen(laptop) {
          top: -14px;
        }
      }
    }
  }
  &__advantages {
    margin-top: 45px;
    display: grid;
    align-items: center;
    row-gap: 30px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    @include screen(laptop) {
      margin-top: 54px;
      row-gap: 0;
      grid-template-columns: repeat(6, minmax(0, 1fr));
    }
  }
  &__advantage {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    gap: 20px;
    svg {
      width: 45px;
      height: 45px;
      @include screen(laptop) {
        width: auto;
        height: auto;
      }
    }
    &:after {
      content: '';
      position: absolute;
      right: 0;
      height: 35px;
      width: 1px;
      background-color: #DAD8E1;
    }
    &:nth-child(2n) {
      &:after {
        display: none;
        @include screen(laptop) {
          display: block;
        }
      }
    }
    &:last-child {
      &:after {
        display: none;
      }
    }
    span {
      font-size: 14px;
      line-height: 21px;
      text-align: center;
      @include screen(laptop) {
        font-size: 16px;
      }
    }
  }
}