.pagination {
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  @include screen(laptop) {
    margin-top: 50px;
  }
  &__links {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    @include screen(laptop) {
      justify-content: flex-start;
    }
  }
  &__link {
    width: 27px;
    height: 27px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 12px;
    @include screen(laptop) {
      width: 32px;
      height: 32px;
      transition: background-color $transition-default;
      &:hover {
        background-color: #F2F3F5;
      }
    }
    &.active {
      background-color: #F2F3F5;
    }
    &-prev {
      margin-right: 19px;
    }
    &-next {
      margin-left: 19px;
    }
    &-disabled {
      opacity: 0.5;
    }
    &-prev,
    &-next {
      width: 43px;
      background-color: #F2F3F5;
      height: 43px;
      transition: opacity $transition-default;
      &:hover {
        opacity: 1;
      }
    }
  }
}